* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.bg {
    background-color: #202020;
    width: 100%;
    height: 100vh;
}

#name{
    position: absolute;
    margin: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: fill 0.5s ease forwards 3s;
}

#name path:nth-child(1){
    stroke-dasharray: 452.048;
    stroke-dashoffset: 452.048;
    animation: line-anim 2s ease forwards;
}

#name path:nth-child(2){
    stroke-dasharray: 475.136;
    stroke-dashoffset: 475.136;
    animation: line-anim 2s ease forwards 0.2s;
}

#name path:nth-child(3){
    stroke-dasharray: 435.816;
    stroke-dashoffset: 435.816;
    animation: line-anim 2s ease forwards 0.4s;
}

#name path:nth-child(4){
   stroke-dasharray: 537.320; 
   stroke-dashoffset: 537.320;
    animation: line-anim 2s ease forwards 0.6s;
}

#name path:nth-child(5){
    stroke-dasharray: 435.815;
    stroke-dashoffset: 435.815;
    animation: line-anim 2s ease forwards 0.8s;
}

#name path:nth-child(6){
    stroke-dasharray: 455.423;
    stroke-dashoffset: 455.423;
    animation: line-anim 2s ease forwards 1.0s;
}

#name path:nth-child(7){
    stroke-dasharray: 330.798;
    stroke-dashoffset: 330.798;
    animation: line-anim 2s ease forwards 1.2s;
}

#name path:nth-child(8){
    stroke-dasharray: 435.815;
    stroke-dashoffset: 435.815;
    animation: line-anim 2s ease forwards 1.4s;
}

#name path:nth-child(9){
    stroke-dasharray: 299.264;
    stroke-dashoffset: 299.264;
    animation: line-anim 2s ease forwards 1.6s;
}

#name path:nth-child(10){
    stroke-dasharray: 439.035;
    stroke-dashoffset: 439.035;
    animation: line-anim 2s ease forwards 1.8s;
}

@keyframes line-anim {
    to {
        stroke-dashoffset: 0;
    }
}

@keyframes fill {
    from {
        fill: transparent;
    }
    to {
        fill: white;
    }
}